/* 动画 */
.icon-animation{
  -webkit-animation:icon 1s linear;
  animation:icon 1s linear;
}
@-webkit-keyframes icon{
  0%{
    -webkit-transform:translateX(0);
    transform:translateX(0);
  }
  25%{
    -webkit-transform:translateX(-10%);
    transform:translateX(-10%);
  }
  50%{
    -webkit-transform:translateX(0);
    transform:translateX(0);
  }
  75%{
    -webkit-transform:translateX(10%);
    transform:translateX(10%);
  }
  100%{
    -webkit-transform:translateX(0);
    transform:translateX(0);
  }
}
@keyframes icon{
  0%{
    -webkit-transform:translateX(0);
    transform:translateX(0);
  }
  25%{
    -webkit-transform:translateX(-10%);
    transform:translateX(-10%);
  }
  50%{
    -webkit-transform:translateX(0);
    transform:translateX(0);
  }
  75%{
    -webkit-transform:translateX(10%);
    transform:translateX(10%);
  }
  100%{
    -webkit-transform:translateX(0);
    transform:translateX(0);
  }
}
.prompt-animation{
  -webkit-animation:prompt 1s linear;
          animation:prompt 1s linear;
}
@-webkit-keyframes prompt{
  0%{
    -webkit-transform:translate(0,0) scale(1);
            transform:translate(0,0) scale(1)
  }
  100%{
    -webkit-transform:translate(3rem,4rem) scale(0);
            transform:translate(3rem,4rem) scale(0)
  }
}
@keyframes prompt{
  0%{
    -webkit-transform:translate(0,0) scale(1);
            transform:translate(0,0) scale(1)
  }
  100%{
    -webkit-transform:translate(3rem,4rem) scale(0);
            transform:translate(3rem,4rem) scale(0)
  }
}
